.avatar{
    display: inline-block;
}

.user{
    margin: 30px 60px;
    border: 2px solid gray ;
    background-color: burlywood;
    padding: 30px;
    border-radius: 20px;
}

.users{
    padding: 10px;
    height: 1250px;
}

.img{
    height: 80px;
    width: 80px;
    border-radius: 50%;
}

.info{
    display: inline-block;
    margin-left: 30px;
    width: 200px;
}

.name{
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    color: blue;
    text-decoration: none;
}

.line{
    display: inline-block;
    font-size: 14px;
    margin-right: 8px;
    color: rgb(92, 84, 84);
}

.status{
    display: inline-block;
    width: 400px; 
    position: relative;
    bottom: 40px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 20px;
}
.location{
    position: relative;
    top: 10px;
}

.btn_sec{
    display: inline-block;
}

.btn{
    width: 100px;
    height: 50px;
    position: relative;
    left: 60px;
    bottom: 30px;
    border-radius: 20px;
    background-color: rgb(75, 149, 235)
    ;
}

.selectedPage{
    font-weight: bold;
    color: blue;
    font-size: 25px;
}

.page{
    cursor: pointer;
    display: inline;
    margin-left: 5px;
}

.pages{
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    margin: 15px;
}

.nav{
    text-decoration: none;
    color: blue;
}