.selectedPage{
    font-weight: bold;
    color: blue;
    font-size: 25px;
}

.page{
    cursor: pointer;
    padding: 0px 2px;
    display: inline;
    margin-left: 0px;
    border: 1px black solid;
}

.pages{
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    margin: 15px;
}

.btn{
    margin: 0px 20px;
    height: 30px;
    width: 60px;
}

#prev{
    border-radius: 0px 50%;
}

#next{
    border-radius: 50% 0px;
}